.game-container {
  font-family: var(--game-font);
  flex-direction: column;
  justify-content: space-between;
  max-width: 300px;
  height: 100vh;
  margin: 0 auto;
  display: flex;
}

.middle-game-section {
  align-items: center;
  gap: 15px;
  display: flex;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.timer-container {
  margin: auto;
  position: relative;
}

.timer-container.inactive {
  opacity: 0;
}

.loader {
  border: 12px solid #f1f1f1;
  border-top-color: #000;
  border-radius: 50%;
  width: 180px;
  height: 180px;
  animation: 1s linear infinite spin;
}

.timer__counter {
  font-size: 80px;
  font-weight: 700;
  position: absolute;
  top: 30%;
  left: 40%;
}

.hands-game-container {
  justify-content: space-between;
  height: 160px;
  display: flex;
}

.hands-game-container.rotate {
  pointer-events: none;
  transform: rotate(180deg);
}

.computer-hand {
  opacity: .5;
}

.user-hand, .computer-hand {
  transition: transform .5s;
}

.user-hand.active {
  pointer-events: none;
}

.computer-hand.active {
  opacity: 1;
}

.user-hand.active.paper {
  transform: translateY(-50%);
}

.computer-hand.active.paper {
  transform: translate(5px, -100%);
}

.user-hand.active.scissors {
  transform: translate(-120px, -55%);
}

.computer-hand.active.scissors {
  transform: translate(-105px, -110%);
}

.user-hand.active.rock {
  transform: translate(95px, -55%);
}

.computer-hand.active.rock {
  transform: translate(110px, -125%);
}

.user-hand.inactive {
  opacity: .5;
  pointer-events: none;
  transform: translateY(50%);
}

